import React, { Component } from 'react';
import Button from 'react-bootstrap/Button';
import TagsAPI from '../api/tags';

class TagCloud extends Component {
  render() {
    return (
        <div className="App" className="mb-3">
        <h5>TAG CLOUD</h5>
        <div className="mb-3">
            {
              TagsAPI.all().map(t=>(
                <span key={t.id}><Button variant="secondary" className="btn-xs mb-2 mr-2 btn-sm" >{t.title}</Button></span>
              ))
            }
        </div>
        </div>
       
    );
  }
}

export default TagCloud;