import React, { Component } from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron';
import BlogTitleAPI from '../api/blogTitle';


class BlogTitle extends Component {
  render() {
    return (
      <div className="App">
            <Jumbotron>
              { 
                  BlogTitleAPI.all().map(b=>(
                    <div key={b.id}>
                      <h1>{b.title}</h1>
                      <p>
                          {b.description}
                      </p>
                    </div>
                  ))
              }
            </Jumbotron>
      </div>
    );
  }
}

export default BlogTitle;