import React, { Component } from 'react';
import {Container, Row, Col} from 'react-bootstrap';
import GalleryAPI from '../api/gallery'


class Gallery extends Component {
  render() {
    return (
        <div className="App" className="mb-3">
            <h5>GALLERY</h5>
            <Container>
                <Row className="mb-0">
                    {
                        GalleryAPI.all().map(g=>(
                            <Col xs={6} md={4} className="mb-3" key={g.id}>
                                <img src={"../images/"+g.image_name} className="img-thumbnail"/>
                            </Col>
                        ))  
                    }
                   
                </Row>
            </Container>
        </div>
    );
  }
}

export default Gallery;