import React, { Component } from 'react';
import Card from 'react-bootstrap/Card';
import {ListGroup, Jumbotron, Col, Image} from 'react-bootstrap';
import Button from 'react-bootstrap/Button';
import BlogListAPI from '../api/blogList' 
import NavbarCollapse from 'react-bootstrap/NavbarCollapse';


class BlogContent extends Component {
 
  render() { 
  {
    
    const blogData = BlogListAPI.get(parseInt(this.props.blogId, 10))
  
  
    return (
     
     <div className="App">
          <div className="row">
              <div className="col-sm-12">
                
                  <div style={{width:"100%",height:"300px"}}><img src={"../images/"+blogData.blog_image} style={{height:"100%", width:"100%"}}/></div>
              </div>
          </div>

          <div className="row mt-4">
              <div className="col-sm-3">
                    <ListGroup as="ul">
                        <ListGroup.Item as="li" active>
                            {blogData.blog_date}
                        </ListGroup.Item>
                        <ListGroup.Item as="li" disabled> {blogData.blog_author.name}</ListGroup.Item>
                        <ListGroup.Item as="li" disabled>
                          {blogData.blog_comment_count} Comments
                        </ListGroup.Item>
                        <ListGroup.Item as="li" disabled>{blogData.blog_likes_count} Likes</ListGroup.Item>
                    </ListGroup>
              </div>
            <div className="col-sm-9">
                <Card className="mb-3">
                    <Card.Body>
                      <Card.Title><h2>{blogData.title}</h2></Card.Title>
                      <Card.Text>
                          {blogData.description}          
                      </Card.Text>
                      <b>Tag:</b>&nbsp;
                      {
                        blogData.blog_tags.map(bt=>(
                          <span>{bt.tag}&nbsp;/&nbsp;</span>
                        ))
                      }
                    </Card.Body>
                </Card>
            </div>
          </div>
          <div>
                <Jumbotron>
                  <div className="row">
                      <div className="col-sm-3">
                          <Col>
                            <Image src={"../images/"+blogData.blog_author.author_image} width={100} height={100} roundedCircle />
                          </Col>
                      </div>
                      <div className="col-sm-9">
                          <h3>{blogData.blog_author.name}</h3>
                          <p className="lead">
                          {blogData.blog_author.about_author}
                          </p>
                          <p><strong>Web:</strong> <a href="#"> {blogData.blog_author.author_website}</a></p>
                      </div>
                  </div>
                </Jumbotron>
          </div>
          <div>
            <div>
                <h4> {blogData.blog_comment_count} Comments</h4>
            </div>
            {
                blogData.blog_comment.map(bc=>(
                <div>
                    <Card className="mb-3, mt-4">
                        <div className="row">
                            <div className="col-sm-3">
                                    <Col className="mt-3">
                                        <Image src={"../images/"+bc.comment_author_image} width={100} height={100} roundedCircle />
                                    </Col>
                            </div>
                            <div className="col-sm-9">
                                    <Card.Body>
                                        <Card.Title>
                                          <h6>{bc.comment_author}</h6>
                                          <span><h6>{bc.comment_date} AT {bc.comment_time}</h6></span>
                                        </Card.Title>
                                        <Card.Text>
                                         {bc.comment_text}
                                        </Card.Text>
                                        <Button variant="danger" className="pull-right">Reply</Button>
                                    </Card.Body>
                            </div>
                        </div>
                    </Card>  
                </div>
              )) 
            }   
        </div>

     
            
            
      </div>
    );
  }
  }
}

export default BlogContent;